import React from 'react';
import style from './FrezernayaObrabotkaMobile.module.css'
import SliderShow from "../SliderShowMobile/SliderShowMobile";
import backArrow from '../../../assets/imgs/onHomeArrow.svg'
import {dataFromSlider} from '../../FrezernayaObrabotka/content'
import {NavLink} from "react-router-dom";


const FrezernayaObrabotkaMobile = () => {

    return (
        <div className={style.block}>
            <NavLink className={style.link} to={'/'}> <img  className={style.imgLink} src={backArrow} alt={''}/> </NavLink>
            <div className={style.wrapperBlock}>
                <h2>Фрейзерная обработка</h2>
                <p>Фрезерование (фрезерная обработка) — это механическая обработка резанием плоскостей, пазов, лысок, при которой режущий инструмент (фреза) совершает вращательное движение,
                    а обрабатываемая заготовка — поступательное.
                    Наша компания располагает как универсально-фрезерными станками
                    (позволяющие осуществлять горизонтальное и вертикальное фрезерование,
                    а также фрезерование под разными углами различным инструментом в ручном
                    и полу-автоматическом режиме) так и фрезерными трех-осевыми обрабатывающими
                    центрами с ЧПУ (позволяющие осуществлять горизонтальное и вертикальное фрезерование,
                    а также фрезерование под разными углами различным инструментом в автоматическом режиме).</p>
            </div>
                <SliderShow obj={dataFromSlider}/>

        </div>
    )
}
export default FrezernayaObrabotkaMobile