import React from 'react';
import style from './TokarnayaObrabotkaMobile.module.css'
import SliderShowMobile from "../SliderShowMobile/SliderShowMobile";
import {dataFromSlider} from '../../TokarnayaObrabotka/content'
import {NavLink} from "react-router-dom";
import backArrow from "../../../assets/imgs/onHomeArrow.svg";


const TokarnayaObrabotkaMobile = () => {

    return (
        <div className={style.block}>

            <NavLink className={style.link} to={'/'}> <img  className={style.imgLink} src={backArrow} alt={''}/> </NavLink>
            <div className={style.wrapperBlock}>
                <h2>Токарная обработка</h2>
                <p>Токарная обработка — это механическая обработка резанием наружных и внутренних поверхностей вращения, в том числе цилиндрических и конических, торцевание, отрезание, снятие фасок, обработка галтелей, прорезание канавок, нарезание внутренних и наружных резьб на токарных станках.
                    Точение, наиболее распространенный метод изготовления деталей типа тел вращения (валов, дисков, осей, пальцев, цапф, фланцев, колец, втулок, гаек, муфт и др.) на токарных станках.
                    Наша компания располагает широкими возможностями по токарной обработке деталей на универсальных токарно-винторезных станках, токарно-карусельных станках и на обрабатывающих центрах с ЧПУ.</p>
            </div>
            <SliderShowMobile obj={dataFromSlider}/>

        </div>
    )
}
export default TokarnayaObrabotkaMobile